import { Contest } from './contest';
import { Teacher } from './teacher';

// 
export class Schedule {
  scheduleId: number;//id
  comid: number;//
  teacherno: number;//
  content: string;//
  position: string;//
  date: string;//
  hours: number;//

  constructor(
    scheduleId: number,
    comid: number,
    teacherno: number,
    content: string,
    position: string,
    date: string,
    hours: number) {
    this.scheduleId = scheduleId;
    this.comid = comid;
    this.teacherno = teacherno;
    this.content = content;
    this.position = position;
    this.date = date;
    this.hours = hours;
  }

  static newInstance(schedule: Schedule): Schedule {
    return new Schedule(
      schedule.scheduleId,
      schedule.comid,
      schedule.teacherno,
      schedule.content,
      schedule.position,
      schedule.date,
      schedule.hours
    )
  }
}



