import { Component, OnInit } from '@angular/core';

import { LoginService } from '../_services';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css'],
  providers: [LoginService]
})
export class UserComponent implements OnInit {
  private user: any;
  private role: any;
  links: Object[];

  constructor(private loginService: LoginService) { }

  ngOnInit() {
    this.user = this.loginService.getUser();
    this.role = this.loginService.getRole();
    if (this.user)
      switch (this.role) {
        case "admin": {
          this.links = [
            // { href: '/admin', name: '' },
            { href: 'article', name: '' },
            { href: 'contest', name: '' },
            { href: 'works', name: ''},
            { href: 'files', name: ''},
            { href: 'material', name: ''}
          ];
        }
          break;
        case "teacher": {
          this.links = [
            { href: '/teacher', name: '', icon:'person' },
            { href: 'declaration', name: '', icon:'create' },
            { href: 'save', name: '()'},
            { href: 'declarationList', name: '', icon:'view_list' },
            { href: 'feedbackAward', name: '', icon:'feedback'}
          ];
          if (this.user.examiner === 1) {
            this.links.splice(1,2);
            this.links.push({ href: 'examiner', name: '', icon:'rate_review' });
          }
        }
          break;
        case "student": {
          this.links = [
            { href: '/student', name: '', icon:'person' },
            { href: 'contest', name: '', icon:'star' },
            { href: 'group', name: ''},
            { href: 'award', name: '', icon:'info' }
          ];
        }
          break;
        default: ;
      }
    if (this.role === "teacher" || this.role === "student") {
      this.links.push({ href: '/notification', name: '', icon:'message' });
    }
  }

}



