import { Contest } from './contest';

// 
export class Hour {
  hoursid: number;//id
  comid: number;//
  type: string;//
  hours: number;//
  remarks: string;//

  constructor(hoursid: number, comid: number, type: string, hours: number, remarks: string) {
    this.hoursid = hoursid;
    this.comid = comid;
    this.type = type;
    this.hours = hours;
    this.remarks = remarks;
  }

  static newInstance(hour: Hour): Hour{
    return new Hour(
      hour.hoursid,
      hour.comid,
      hour.type,
      hour.hours,
      hour.remarks
    );
  }
}



