import { Group } from './group';
import { Student } from './student';

// 
export class GroupDetail {
  id: number;//id TODO Contest
  groups: Group;// TODO ?
  student: Student;//

  Id(id: number): GroupDetail {
    this.id = id;
    return this;
  }
  Groups(groups: Group): GroupDetail {
    this.groups = groups;
    return this;
  }
  Student(student: Student): GroupDetail {
    this.student = student;
    return this;
  }
}



