import { Contest } from './contest';
import { Level } from './level';
import { Group } from './group';

//  TODO 
export class Project{
    competition: Contest;//
    comName: string;//
    level: Level;//
    hunit: string;// TODO hUnit
    sOrganizer: string;//
    introduction: string;//
    applyBeginDate: string;//
    applyEndDate: string;//
    comDate: string;//
    cost: number;//
    status: number;//
    isPublish: number;//
    isGroup: number;//

    Competition(competition: Contest): Project {
      this.competition = competition;
      return this;
    }
    ComName(comName: string): Project {
      this.comName = comName;
      return this;
    }
    Level(level: Level): Project {
      this.level = level;
      return this;
    }
    Hunit(hunit: string): Project {
      this.hunit = hunit;
      return this;
    }
    SOrganizer(sOrganizer: string): Project {
      this.sOrganizer = sOrganizer;
      return this;
    }
    Introduction(introduction: string): Project {
      this.introduction = introduction;
      return this;
    }
    ApplyBeginDate(applyBeginDate: string): Project {
      this.applyBeginDate = applyBeginDate;
      return this;
    }
    ApplyEndDate(applyEndDate: string): Project {
      this.applyEndDate = applyEndDate;
      return this;
    }
    ComDate(comDate: string): Project {
      this.comDate = comDate;
      return this;
    }
    Cost(cost: number): Project {
      this.cost = cost;
      return this;
    }
    Status(status: number): Project {
      this.status = status;
      return this;
    }
    IsPublish(isPublish: number): Project {
      this.isPublish = isPublish;
      return this;
    }
    // Groups(groups: Group[]): Project {
    //   this.groups = groups;
    //   return this;
    // }
}



